<template>
  <div class="card">
    <div class="card-header pb-0">
      <h4 class="pl-3">{{ name }}</h4>
    </div>
    <div class="card-body pt-0">
      <div class="p-0 rounded">
        <pre class="text-white user-select-all bg-secondary p-2 rounded-2">{{
          code
        }}</pre>

        <button
          type="button"
          class="btn btn-primary btn-sm"
          v-clipboard:copy="code"
          v-clipboard:success="onCopy"
          v-clipboard:error="onError"
        >
          Copy
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { inject } from "vue";

export default {
  props: {
    name: String,
    code: String,
  },
  data() {
    return {};
  },
  mounted() {
    //this.$toast.show("Toast from mounted!");
  },
  setup(props) {
    const toast = inject("toast");

    const onCopy = (e) => {
      console.log(e.text);

      toast.show(props.name + ` | Copied!`);
    };
    const onError = (e) => {
      console.log("Failed to copy texts" + e);
    };

    return { onCopy, onError };
  },
  methods: {},
};
</script>

