<script>
import firebase from "firebase/app";
import "firebase/database";

var config = {
  apiKey: "AIzaSyBU7CL_rsDnh96zYBpPS0yXPBXWBGPG4-k",
  authDomain: "wikode-d85b6.firebaseapp.com",
  databaseURL: "https://wikode-d85b6-default-rtdb.firebaseio.com",
  projectId: "wikode-d85b6",
  storageBucket: "wikode-d85b6.appspot.com",
  messagingSenderId: "696118844197",
  appId: "1:696118844197:web:ca40b3990a3dd5d462d8cc",
  measurementId: "G-DQNNH2LPFF",
};

// Initialize Firebase
firebase.initializeApp(config);
//firebase.analytics();

const database = firebase.database();
const commandsRef = database.ref("commands");

export default {
  data() {
    return {
      active: {},
      groups: [],
      commands: [],
      menu: [
        {
          name: "Git",
          slug: "git",
          icon: "terminal",
          active: false,
          items: [],
          /* items: [
            {
              key: "git-basic",
              name: "Basic",
              slug: "basic",
              active: false,
            },
            {
              key: "git-advanced",
              name: "Advanced",
              slug: "advanced",
              active: false,
            },
          ],
          */
        },
        {
          name: "npm",
          slug: "npm",
          icon: "terminal",
          active: false,
          items: [],
        },
      ],
    };
  },
  created() {
    this.getCommands("git");
    this.setItemActive("git");
  },
  methods: {
    getCommands(topic = null) {
      commandsRef.get().then((response) => {
        response.forEach((data) => {
          //console.log(data.key);
          if (!topic || data.key === topic) {
            const groups = [];
            data.forEach((gr) => {
              const commands = [];
              gr.forEach((cm) => {
                commands.push(cm.val()); //const group = gr.val();
              });
              groups.push({
                name: "",
                slug: gr.key,
                commands: commands,
              });
              this.groups = groups;
              console.log(this.groups);
            });
          }
        });
      });
    },

    getMenu() {
      return [];
    },

    setItemActive(slug) {
      this.menu.find((item, i) => {
        if (item.slug === slug) {
          this.menu[i].active = true;
          if (this.menu[i].items.length === 0) {
            this.getCommands(item.slug);
            this.active = item;
          }
        }

        else {
          this.menu[i].active = false;
        }
        
      });
    },

    setSubItemActive(parent_slug, slug) {

      this.menu.forEach((item, index)=> {
        item.items.find((subitem, i) => {
          this.menu[index].items[i].active = item.slug === parent_slug && subitem.slug === slug;
        });
      });
    },
  },
};
</script>

